import React from 'react'
import './dashboard.scss'
import BarChartD3 from "../Charts/BarChartD3";
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import ListIcon from '@material-ui/icons/List';
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import TwoLineGraph from "../Charts/TwoLineGraph";
import httpService from "../services/Http.service";

export default function Step5(props) {
    const [data, setData] = React.useState({});
    const [percentFirst, setPercentFirst] = React.useState(0);
    const [returnOnAssetRoe, setReturnOnAssetRoe] = React.useState(0);
    const [returnOnAssetRoa, setReturnOnAssetRoa] = React.useState(0);
    const [returnOnAssetRoeIn, setReturnOnAssetRoeIn] = React.useState(0);
    const [returnOnAssetRoaIn, setReturnOnAssetRoaIn] = React.useState(0);
    React.useEffect(() => {
        if(props.data.ticker){
            httpService.httpGet(`stocks/financialStats?items=[grNIL3y,sales,netIncomeLoss,fcff,netCashFlowFromOperatingActivities]&tickers=${props.data.ticker}&fiscalMonths=6,12&fiscalYears=[2017,null]`).then(res => {
                if (res&&res.isSuccess) {
                    setData(res.data)
                    if(res.data.tickers[0]){
                        const items=res.data.tickers[0].items;
                        let tempData = 0;

                        for (let item of items) {
                            if (item.item === 'grnil3y') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempData+=sub.values.value
                                    }
                                }
                            }
                        }
                        setPercentFirst(tempData*100)
                    }
                }
            });
            httpService.httpGet(`stocks/financialStats?items=[roe,roa]&tickers=${props.data.ticker}&fiscalMonths=12&fiscalYears=[2017,null]`).then(res => {
                if (res&&res.isSuccess) {
                    setData(res.data)
                    console.log('res.data',res.data)
                    if(res.data.tickers[0]){
                        const items=res.data.tickers[0].items;
                        let tempData = 0;
                        let tempDataRoa = 0;

                        for (let item of items) {
                            if (item.item === 'roe') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempData=sub.values.value
                                    }
                                }
                            }
                            if (item.item === 'roa') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempDataRoa=sub.values.value
                                    }
                                }
                            }
                        }
                        setReturnOnAssetRoe(tempData*100)
                        setReturnOnAssetRoa(tempDataRoa*100)
                    }
                }
            });
            httpService.httpGet(`indices/financialStats?items=[roe,roa]&tickers=${props.data.ticker}&fiscalMonths=12&fiscalYears=[2017,null]&tickerTypes=industryIndex&exchanges=IREX`).then(res => {
                if (res&&res.isSuccess) {
                    setData(res.data)
                    console.log('res.data',res.data)
                    if(res.data.tickers[0]){
                        const items=res.data.tickers[0].items;
                        let tempData = 0;
                        let tempDataRoa = 0;

                        for (let item of items) {
                            if (item.item === 'roe') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempData=sub.values.value
                                    }
                                }
                            }
                            if (item.item === 'roa') {
                                for (let sub of item.days) {
                                    if (sub.values&&sub.values.value) {
                                        tempDataRoa=sub.values.value
                                    }
                                }
                            }
                        }
                        setReturnOnAssetRoeIn(tempData*100)
                        setReturnOnAssetRoaIn(tempDataRoa*100)
                    }
                }
            });
        }

    }, [])
    return (
        <div className='card dark-card'>
            <div className='card-body'>
                <div className='col-md-12'>
                    <div className='row'>
                        <div className='col-md-9'>
                            <div className='d-flex section-detail align-items-center'>
                                <span className='num'>۵</span>
                                <div className='line'></div>
                                <span className='title'>عملکرد گذشته</span>
                            </div>

                            <div className='d-flex'>
                                <p className='question-line'>
                                    {props.data.nameFA} طی 5 سال گذشته چگونه عمل کرده است؟
                                </p>
                            </div>

                            <div className='d-flex flex-column'>
                                <h1 className='text-white'>
                                    {percentFirst}X
                                </h1>
                                <p>
                                    تاریخچه رشد درآمد سالانه
                                </p>
                            </div>
                        </div>

                        <div className='col-md-3'>
                            <div className='d-flex flex-column'>
                               <span className='analysis'>
                                   بررسی های تحلیل
                                   <span className='text-white-50 mr-2'>۱/۶</span>
                               </span>

                                <div className='d-flex'>
                                    <CheckCircleIcon style={{fill: "#2BD784"}}/>
                                    <HighlightOffIcon style={{fill: '#E64141'}}/>
                                    <HighlightOffIcon style={{fill: '#E64141'}}/>
                                    <HighlightOffIcon style={{fill: '#E64141'}}/>
                                    <HighlightOffIcon style={{fill: '#E64141'}}/>
                                    <CheckCircleIcon style={{fill: "#2BD784"}}/>
                                </div>
                            </div>
                        </div>
                    </div>

                    <hr/>

                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۱</span>
                        <div className='line'></div>
                        <span className='title'>تاریخچه درآمد  </span>
                    </div>

                    <div className='col-md-12'>
                        {(data&&data.tickers&&data.tickers.length>0)?<TwoLineGraph data={data}/>:null}
                    </div>
                    <div className='d-flex justify-content-end mt-4 mb-4'>
                        <button className='btn-light-outline ml-1'>
                            <ListIcon/>
                            نمایش داده
                        </button>

                        <button className='btn-light-outline'>
                            <MoreHorizIcon/>
                        </button>
                    </div>

                    <div className='col-md-12'>
                        <div className='d-flex'>
                            <CheckCircleIcon style={{fill: "#2BD784"}}/>
                            <p className='more-detail'>
                        <span>
                       درآمد با کیفیت:
                    </span>
                                AAPL درآمد با کیفیت بالا دارد.
                            </p>
                        </div>

                        <div className='d-flex'>
                            <HighlightOffIcon style={{fill: '#E64141'}}/>
                            <p className='more-detail-red'>
                        <span>
                        رشد سود :
                    </span>
                                حاشیه سود خالص فعلی AAPL (21.2٪) پایین تر از سال گذشته (22.4٪) است.
                            </p>
                        </div>
                    </div>
                    <hr/>

                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۲</span>
                        <div className='line'></div>
                        <span className='title'>تجزیه و تحلیل رشد درآمد گذشته  </span>
                    </div>


                    <div className={"row"}>
                        <div className={"col-lg-6 col-md-12 col-sm-12 col-xs-12"}>
                            {(props.data)?<BarChartD3 data={props.data}/>:null}


                            <div align={"right"} className={'bar-chart-name'}>
                                رشد ۵ سال گذشته
                            </div>
                        </div>

                        {/*<div className={"col-lg-6 col-md-12 col-sm-12 col-xs-12"}>*/}
                        {/*    <BarChartD3/>*/}
                        {/*    <div align={"right"} className={'bar-chart-name'}>*/}
                        {/*        رشد ۵ سال گذشته*/}
                        {/*    </div>*/}
                        {/*</div>*/}
                    </div>

                    <div className='d-flex justify-content-end mt-4 mb-4'>
                        <button className='btn-light-outline'>
                            <MoreHorizIcon/>
                        </button>
                    </div>

                    <div className='col-md-12'>
                        <div className='d-flex'>
                            <CheckCircleIcon style={{fill: "#2BD784"}}/>
                            <p className='more-detail'>
                        <span>روند سود :
                        </span>
                                درآمد AAPL طی 5 سال گذشته 4.8٪ در سال رشد داشته است.
                            </p>
                        </div>

                        <div className='d-flex'>
                            <HighlightOffIcon style={{fill: '#E64141'}}/>
                            <p className='more-detail-red'>
                        <span>
                        سرعت بخشیدن به رشد :
                    </span>
                                AAPL در طی سال گذشته رشد منفی داشته است ، بنابراین نمی توان آن را با میانگین 5 ساله خود
                                مقایسه کرد.
                            </p>
                        </div>

                        <div className='d-flex'>
                            <HighlightOffIcon style={{fill: '#E64141'}}/>
                            <p className='more-detail-red'>
                        <span>
                         درآمد در مقابل صنعت:
                    </span>
                                AAPL طی سال گذشته رشد منفی (-7.1٪) داشته و مقایسه آن با میانگین صنعت فناوری (25.2٪)
                                دشوار است.
                            </p>
                        </div>
                    </div>
                    <hr/>
                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۳</span>
                        <div className='line'></div>
                        <span className='title'>بازده سهام  </span>
                    </div>

                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-6'>
                                {/*<GaugeDiagram/>*/}
                                <h1 className='text-white'>
                                    {Math.round(returnOnAssetRoe * 100) / 100}%
                                </h1>

                                <div className='d-flex justify-content-end mt-4 mb-4'>
                                    <button className='btn-light-outline'>
                                        <MoreHorizIcon/>
                                    </button>
                                </div>
                            </div>
                            <div className='col-md-6'>
                                <div className='d-flex'>
                                    <HighlightOffIcon style={{fill: '#E64141'}}/>
                                    <p className='more-detail-red'>
                        <span>
                         بالا <bdi>ROE</bdi> :
                    </span>
                                        در حالی که بازده سهام AAPL ({returnOnAssetRoe}٪) برجسته است ، این متریک به دلیل سطح بالای
                                        بدهی آنها کاهش یافته است.
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr/>

                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۴</span>
                        <div className='line'></div>
                        <span className='title'>بازده دارایی  </span>
                    </div>

                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-6'>
                                {/*<GaugeDiagram/>*/}
                                <h1 className='text-white'>
                                    {Math.round(returnOnAssetRoa * 100) / 100}%
                                </h1>

                                <div className='d-flex justify-content-end mt-4 mb-4'>
                                    <button className='btn-light-outline'>
                                        <MoreHorizIcon/>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr/>

                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۵</span>
                        <div className='line'></div>
                        <span className='title'>بازده سرمایه اشتغال  </span>
                    </div>

                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-6'>
                                {/*<GaugeDiagram/>*/}
                                <h1 className='text-white'>
                                    {Math.round(returnOnAssetRoaIn * 100) / 100}%
                                </h1>

                                <div className='d-flex justify-content-end mt-4 mb-4'>
                                    <button className='btn-light-outline'>
                                        <MoreHorizIcon/>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr/>
                    <div className='d-flex small-section-detail align-items-center'>
                        <span className='num'>۵.۶</span>
                        <div className='line'></div>
                        <span className='title'> Return on asset</span>
                    </div>
                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-6'>
                                {/*<GaugeDiagram/>*/}
                                <h1 className='text-white'>
                                    {Math.round(returnOnAssetRoaIn * 100) / 100}%
                                </h1>

                                <div className='d-flex justify-content-end mt-4 mb-4'>
                                    <button className='btn-light-outline'>
                                        <MoreHorizIcon/>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='col-md-12'>
                        <p className='next-page'>
                            شرکت های عملکرد قوی گذشته را در صنعت فناوری کاوش کنید.
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}
