import React from 'react'
import OneLineChart from "../Charts/OneLineChart";
import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import './dashboard.scss'
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import httpService from "../services/Http.service";

import Highcharts from 'highcharts/highstock';
import HighchartsReact from 'highcharts-react-official';

export default function Step2(props) {

    const [chart, setChart] = React.useState({
        series: [
            {
                data: [1, 2, 1, 4, 3, 6, 7, 3, 8, 6, 9]
            }
        ]
    });


    const [data, setData] = React.useState({});
    const [week, setWeek] = React.useState({});
    const [month, setMonth] = React.useState({});
    const [year, setYear] = React.useState({});
    const [threeYear, setThreeYear] = React.useState({});
    const [threeMonth, setThreeMonth] = React.useState({});
    const [news, setNews] = React.useState([]);
    React.useEffect(() => {
        if (props.data.ticker) {
            httpService.httpGet(`quotes/?items=price&tickers=${props.data.ticker}&date=[20200101,null]`).then(res => {
                if (res && res.isSuccess) {
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setData(item.days)
                        var chartData = [];
                        for (let day of item.days) {
                            if (day.items.length > 0) {
                                chartData.push(day.items[0].values.close)
                            }
                        }
                        setChart({
                            series: [
                                {
                                    data: chartData
                                }
                            ]
                        })


                    }
                }
            });
            httpService.httpGet(`stocks/financialStats/?items=return1w&tickers=${props.data.ticker}`).then(res => {
                if (res && res.isSuccess) {
                    console.log(res.data)
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setWeek({company:Math.round(item.days[0].values.value*100 * 100) / 100
                        ,industry:Math.round(item.days[0].values.value/2.5*100 * 100) / 100})
                    }
                }
            });
            httpService.httpGet(`stocks/financialStats/?items=return1y&tickers=${props.data.ticker}`).then(res => {
                if (res && res.isSuccess) {
                    console.log(res.data)
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setYear({company:Math.round(item.days[0].values.value*100 * 100) / 100
                            ,industry:Math.round(item.days[0].values.value/2.5*100 * 100) / 100})
                    }
                }
            });
            httpService.httpGet(`stocks/financialStats/?items=return3y&tickers=${props.data.ticker}`).then(res => {
                if (res && res.isSuccess) {
                    console.log(res.data)
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setThreeYear({company:Math.round(item.days[0].values.value*100 * 100) / 100
                            ,industry:Math.round(item.days[0].values.value/2.5*100 * 100) / 100})
                    }
                }
            });
            httpService.httpGet(`stocks/financialStats/?items=return1m&tickers=${props.data.ticker}`).then(res => {
                if (res && res.isSuccess) {
                    console.log(res.data)
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setMonth({company:Math.round(item.days[0].values.value*100 * 100) / 100
                            ,industry:Math.round(item.days[0].values.value/2.5*100 * 100) / 100})
                    }
                }
            });
            httpService.httpGet(`stocks/financialStats/?items=return3m&tickers=${props.data.ticker}`).then(res => {
                if (res && res.isSuccess) {
                    console.log(res.data)
                    if (res.data.tickers[0]) {
                        const item = res.data.tickers[0].items[0];
                        setThreeMonth({company:Math.round(item.days[0].values.value*100 * 100) / 100
                            ,industry:Math.round(item.days[0].values.value/2.5*100 * 100) / 100})
                    }
                }
            });
            httpService.httpGet(`news/filter/?ticker=${props.data.ticker}`).then(res => {
                if (res && res.data) {
                    setNews(res.data);
                }
            })
        }

    }, [])
    return (
        <div className='card dark-card'>
            <div className='card-body'>
                <div className='d-flex section-detail align-items-center'>
                    <span className='num'>۲</span>
                    <div className='line'></div>
                    <span className='title'>قیمت سهم و اخبار</span>
                </div>
                <div className='d-flex'>
                    <p className='question-line'>
                        چگونه قیمت سهم {props.data.nameFA} به مرور زمان انجام شده و چه اتفاقاتی باعث تغییر قیمت شده است؟
                    </p>
                </div>
                <hr/>

                <div className='d-flex small-section-detail align-items-center'>
                    <span className='num'>۲.۱</span>
                    <div className='line'></div>
                    <span className='title'>آخرین قیمت سهم و دوره ها</span>
                </div>

                <div className='d-flex'>
                    <CheckCircleIcon style={{fill: "#2BD784"}}/>
                    <p className='more-detail'>
                        <span>
                        پایداری قیمت سهم :
                    </span>
                        <div className='col-md-12'>
                            <HighchartsReact
                                highcharts={Highcharts}
                                constructorType={'stockChart'}
                                options={chart}
                            />
                        </div>
                    </p>
                </div>
                <hr/>
                <div className='d-flex small-section-detail align-items-center'>
                    <span className='num'>۲.۲</span>
                    <div className='line'></div>
                    <span className='title'>عملکرد</span>
                </div>
                <hr/>

                <div className='d-flex flex-column'>
                    <h5 className='text-white w-100'>
                        خروجی ۷ روز
                    </h5>

                    <div className='col-md-12'>
                        <div className='row'>


                            <div className='col-md-6 report-result-green'>
                                <div className='result-num'>{week.industry} ٪</div>
                                <span>صنعت</span>
                            </div>

                            <div className='col-md-6 report-result-green'>
                                <div className='result-num'>{week.company} ٪</div>
                                <span>شرکت</span>
                            </div>
                        </div>
                    </div>
                </div>

                <hr/>

                <div className='d-flex flex-column mb-4'>
                    <h5 className='text-white w-100'>
                        خروجی ۱ سال
                    </h5>

                    <div className='col-md-12'>
                        <div className='row'>
                            <div className='col-md-4 report-result-green'>
                                <div className='result-num'>{year.industry} ٪</div>
                                <span>صنعت</span>
                            </div>

                            <div className='col-md-4 report-result-green'>
                                <div className='result-num'>{year.company} ٪</div>
                                <span>شرکت</span>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='d-flex'>
                    <CheckCircleIcon style={{fill: "#2BD784"}}/>
                    <p className='more-detail'>
                        <span>
                        خروجی صنعتی :
                    </span>
                        لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است.
                    </p>
                </div>

                <div className='d-flex'>
                    <CheckCircleIcon style={{fill: "#2BD784"}}/>
                    <p className='more-detail'>
                        <span>
                        خروجی بازار :
                    </span>
                        لورم ایپسوم متن ساختگی با تولید سادگی نامفهوم از صنعت چاپ و با استفاده از طراحان گرافیک است.
                    </p>
                </div>
                <div className='col-md-12 mt-3'>
                    <div className='row'>
                        <div className='col-md-6'>
                            <h5 className='text-white w-100'>
                                خروجی سهام
                            </h5>

                            <table className='table dark-table mt-3'>
                                <thead>
                                <tr>
                                    <th></th>
                                    <th>
                                        شرکت
                                    </th>
                                    <th>
                                        صنعت
                                    </th>
                                </tr>
                                </thead>
                                <tbody>
                                <tr>
                                    <td>
                                        ۷ روز
                                    </td>
                                    <td>
                                        {week.company} ٪
                                    </td>
                                    <td>
                                        {week.industry} ٪
                                    </td>
                                </tr>

                                <tr>
                                    <td>
                                        30 روز
                                    </td>
                                    <td>
                                        {month.company} ٪
                                    </td>
                                    <td>
                                        {month.industry} ٪
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        90 روز
                                    </td>
                                    <td>
                                        {threeMonth.company} ٪
                                    </td>
                                    <td>
                                        {threeMonth.industry} ٪
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        یک ساله
                                    </td>
                                    <td>
                                        {year.company} ٪
                                    </td>
                                    <td>
                                        {year.industry} ٪
                                    </td>
                                </tr>
                                <tr>
                                    <td>
                                        سه ساله
                                    </td>
                                    <td>
                                        {threeYear.company} ٪
                                    </td>
                                    <td>
                                        {threeYear.industry} ٪
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                        <div className='col-md-6'>
                            <h5 className='text-white w-100'>
                                نوسانات قیمت در مقابل بازار
                            </h5>

                            <div className='d-flex mt-3'>
                                <p className='question-line'>
                                    قیمت سهم ماکروسافت در مقایسه با بازار و صنعت در 5 سال گذشته چقدر بی ثبات است؟
                                </p>
                            </div>

                            <div className='col-md-12'>
                                <OneLineChart/>
                            </div>
                        </div>
                    </div>
                </div>

                <div className='d-flex justify-content-end mt-5'>
                    <button className='btn-light-outline'>
                        <MoreHorizIcon style={{fill: '#fff'}}/>
                    </button>
                </div>

                <hr/>

                <div className='d-flex small-section-detail align-items-center'>
                    <span className='num'>۲.۳</span>
                    <div className='line'></div>
                    <span className='title'>اخبار</span>
                </div>

                <div className='col-md-12'>
                    <div className='row'>
                        {news.length > 0 ? news.map((item, id) => {
                            return (
                                <div className='col-md-4 news'>
                                    <div className='title'>
                                        {item.title}
                                    </div>
                                    <p className='description'>
                                        {item.description}
                                    </p>
                                </div>
                            )
                        }) : <div>اخباری وجود ندارد</div>}
                    </div>
                </div>

                <div className='d-flex justify-content-end'>
                    <button className='btn-light-outline'>
                        <MoreHorizIcon/>
                    </button>
                </div>
            </div>
        </div>
    )
}
